.HomePage{
  width: 100%;
  height: auto;
  background-color: rgb(187, 247, 246);
}
.Sky{
  width: 100%;
  height: 100vh;
  background-image: url('https://1.bp.blogspot.com/--kflukKM3cA/XvSuQDlb_aI/AAAAAAAAVng/sTA3jBh7KtAtepJ2c5yyDZemzWXXHFzQwCK4BGAsYHg/w976-h549/wallpaper-4k-for-pc--heroscreen.cc.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.eagleImg{
  position: static;
  animation: moveEagle infinite 1s;
}
@keyframes moveEagle {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(20px); /* Adjust the distance as needed */
  }
  100% {
    transform: translateX(0);
  }
}
.nav-link{
  text-decoration: none;
  color: white;
}
.nav-link:hover{
  color: cyan;
}
.BirdsImg{
  transform: scaleX(-1);
}
.SkyImage{
  height: 200vh;
  width: 100%;
}
@media (max-width: 768px) {
  .SkyImage {
    height: 80vh;
  }

  /*.SkyImage p {
    opacity: 0;
  }
  .ImageInParallax{
    opacity: 0;
  }*/
  .ImageInParallax{
    padding: 30px;
    height:200px ;
  }
}

.BirdImg1 {
  width: 100%;
  height: auto; 
  max-height: 50vh;
  object-fit: cover;
}

#FlyingBirds1{
  transform: scaleX(-1);
}
#SkyBtn{
  background-color: white;
  border-radius: 15px;
  width: 200px;
  margin-top: 20px;
  display: flex;
  justify-content: center;
}
#RocketPointing{
  cursor: pointer;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Person{
  animation: RotatePerson infinite 7s;
}

@keyframes RotatePerson {
  100%{
    transform: rotate(360deg);
  }
}


 #Box span{

  color: hsl(0 0% 100% / 0.2);
  background-repeat: no-repeat;
  background-clip: text;
  background-size: 0% 100%;
  background-image: linear-gradient( 90deg,rgb(33, 30, 33), rgb(110, 99, 34));
  animation: scroll-reveal linear forwards;
  animation-timeline: view(y);
  

 }
 .BGChangeTesting{
  animation: BGChange linear forwards;
  animation-timeline: view(y);
 }
 @keyframes BGChange{
  25%{
   
    background-color: #252A36;
    }
  75%{
   
  background-color: #155263;
  }
  100%{
   
    background-color: #252A36;
    }
 }
#Box #MainTitle span{
  animation-range-start: cover 20vh;
  animation-range-end: cover 30vh;
}
#Box #Para span{
  animation-range-start: cover 22.5vh;
  animation-range-end: cover 50vh;
}

 @keyframes scroll-reveal {
  to{
    background-size: 100% 100%;
    
  }
 }

 #ExploreBtn{
  background-color: rgb(33, 30, 33) ;
  color: rgb(110, 99, 34);
  height: 50px;
  border-radius: 20px;
  font-weight: bold;
 }
 #ExploreBtn:hover{
  background-color: rgb(110, 99, 34);
  color: rgb(33, 30, 33);
  border: 2px solid white;
  
 }